        rulesNUMTAXA () {
            const rules = []
        
            const rule = (!!this.editedItem.NUMTAXA || this.editedItem.ESTADO === 'FEDERAL'|| this.editedItem.EMPRESA.includes('Budel')) || 'Campo Obrigatório'
            rules.push(rule)

            return rules
        },
        
    },

    watch: {
        dialog (val) {
            val || this.close()
        },
        dialogDelete (val) {
            val || this.closeDelete()
        },

        tempPagamentoTaxa () {
            this.editedItem.DATAPAGAMENTOTAXA = this.formatDate(this.tempPagamentoTaxa)
        },
        tempEmissao () {
            this.editedItem.EMISSÃO = this.formatDate(this.tempEmissao)
        },
        tempVencimento () {
            this.editedItem.VENCIMENTO = this.formatDate(this.tempVencimento)
        },
        tempVencimentoBoleto () {
            this.editedItem.VENCIMENTOBOLETO = this.formatDate(this.tempVencimentoBoleto)
        },
    },

    created () {
        this.fetchLicences();
        this.randomPhrase();
    },

    filters: {
        currencyBRL(valor) {
            return valor.toLocaleString("pt-BR", { style: "currency", currency: "BRL" });
        }
    },

    methods: {
        disabledField() {
            if (this.editedIndex > -1) {
                this.isDisabled = true
                if(this.editedItem.VENCIMENTO) {this.vencimentoAetDisabled = true}
                if(this.editedItem.EMISSÃO) {this.emissaoAetDisabled = true}
                if(this.editedItem.AET) {this.numAetDisabled = true}
            }
            if (this.editedItem.ESTADO === 'FEDERAL') {
                if(this.editedItem.DATAPAGAMENTOTAXA) {this.taxDateDisabled = true}
                if(this.editedItem.BANCO) {this.bancoDisabled = true}
                if(this.editedItem.CUSTOTAXA) {this.custoTaxaDisabled = true}
                if(this.editedItem.NUMTAXA) {this.numTaxaDisabled = true}
                if(!this.editedItem.DATAPAGAMENTOTAXA) {this.taxDateDisabled = false}
                if(!this.editedItem.BANCO) {this.bancoDisabled = false}
                if(!this.editedItem.CUSTOTAXA) {this.custoTaxaDisabled = false}
                if(!this.editedItem.NUMTAXA) {this.numTaxaDisabled = false}
            }
            if (this.editedItem.EMPRESA.includes('Budel')) {
                if(this.editedItem.DATAPAGAMENTOTAXA) {this.taxDateDisabled = true}
                if(this.editedItem.BANCO) {this.bancoDisabled = true}
                if(this.editedItem.CUSTOTAXA) {this.custoTaxaDisabled = true}
                if(this.editedItem.NUMTAXA) {this.numTaxaDisabled = true}
                if(!this.editedItem.DATAPAGAMENTOTAXA) {this.taxDateDisabled = false}
                if(!this.editedItem.BANCO) {this.bancoDisabled = false}
                if(!this.editedItem.CUSTOTAXA) {this.custoTaxaDisabled = false}
                if(!this.editedItem.NUMTAXA) {this.numTaxaDisabled = false}
            }
            if (this.editedItem.ESTADO != 'FEDERAL') {
                if(this.editedItem.DATAPAGAMENTOTAXA) {this.taxDateDisabled = true}
                if(this.editedItem.BANCO) {this.bancoDisabled = true}
                if(this.editedItem.CUSTOTAXA) {this.custoTaxaDisabled = true}
                if(this.editedItem.NUMTAXA) {this.numTaxaDisabled = true}
                if(!this.editedItem.DATAPAGAMENTOTAXA) {this.taxDateDisabled = false}
                if(!this.editedItem.BANCO) {this.bancoDisabled = false}
                if(!this.editedItem.CUSTOTAXA) {this.custoTaxaDisabled = false}
                if(!this.editedItem.NUMTAXA) {this.numTaxaDisabled = false}
            }
            if (!this.editedItem.EMPRESA.includes('Budel')) {
                if(this.editedItem.DATAPAGAMENTOTAXA) {this.taxDateDisabled = true}
                if(this.editedItem.BANCO) {this.bancoDisabled = true}
                if(this.editedItem.CUSTOTAXA) {this.custoTaxaDisabled = true}
                if(this.editedItem.NUMTAXA) {this.numTaxaDisabled = true}
                if(!this.editedItem.DATAPAGAMENTOTAXA) {this.taxDateDisabled = false}
                if(!this.editedItem.BANCO) {this.bancoDisabled = false}
                if(!this.editedItem.CUSTOTAXA) {this.custoTaxaDisabled = false}
                if(!this.editedItem.NUMTAXA) {this.numTaxaDisabled = false}
            }
            if (this.editedIndex === -1){
                this.isDisabled = false
                this.vencimentoAetDisabled = false
                this.emissaoAetDisabled = false
                this.numAetDisabled = false
            }
        },

        randomPhrase() {
            return this.fraseAleatoria = Math.floor(Math.random() * this.frasesMotivacionais.length)
        },

        setTimeNow() {
            var date = new Date();
            var day = date.getDate();
            var month = date.getMonth() + 1;
            var year = date.getFullYear();
            if (month < 10) month = "0" + month;
            if (day < 10) day = "0" + day;
            this.now = day + "/" + month + "/" + year;
            this.editedIndex === -1 ? this.editedItem.DATA = this.now : this.editedItem.DATA;

            const m = ["JANEIRO","FEVEREIRO","MARÇO","ABRIL","MAIO","JUNHO","JULHO","AGOSTO","SETEMBRO","OUTUBRO","NOVEMBRO","DEZEMBRO"];
            const d = new Date();
            this.mestodo = m[d.getMonth()];
            this.editedIndex === -1 ? this.editedItem.MÊS = this.mestodo : this.editedItem.MÊS
        },

        formatDate (tempDate) {
            if (!tempDate) return null

            const [year, month, day] = tempDate.split('-')
            return `${day}/${month}/${year}`
        },
        parseDate (tempDate) {
            if (!tempDate) return null

            const [day, month, year] = tempDate.split('/')
            return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
        },

        parseMonetary() {
            this.editedItem.CUSTOTAXA = this.editedItem.CUSTOTAXA.replace('R$ ', '')
            this.editedItem.VALORCOBRADO = this.editedItem.VALORCOBRADO.replace('R$ ', '')
            this.editedItem.VALORBOLETO = this.editedItem.VALORBOLETO.replace('R$ ', '')
            this.editedItem.SALDO = this.editedItem.SALDO.replace('R$ ', '')
        },

        getColor (STATUS) {
            if (STATUS === 'PAGO') return 'blue lighten-4'
            else if (STATUS === 'A VENCER') return 'green lighten-4'
            else if (STATUS === 'TAXA PAGA') return 'purple lighten-4'
            else if (STATUS === 'A COBRAR') return 'yellow lighten-4'
            else if (STATUS === 'VENCIDO') return 'red lighten-4'
            else if (STATUS === 'NÃO COBRAR') return 'red lighten-4'
            else if (STATUS === 'RENEGOCIADO') return 'orange lighten-4'
            else if (STATUS === '') return ''
            else return 'grey lighten-2'
        },

        getIcon (STATUS) {
            if (STATUS === 'PAGO') return 'mdi-check-all blue--text'
            else if (STATUS === 'A VENCER' || STATUS === 'A DEPOSITAR') return 'mdi-cash-clock'
            else if (STATUS === 'TAXA PAGA') return 'mdi-check'
            else if (STATUS === 'A COBRAR') return 'mdi-clock-alert-outline'
            else if (STATUS === 'VENCIDO') return 'mdi-alert red--text'
            else if (STATUS === 'NÃO COBRAR') return 'mdi-cash-remove red--text'
            else if (STATUS === 'RENEGOCIADO') return 'mdi-clock-alert-outline red--text'
            else return 'mdi-list-status'
        },

        fetchLicences() {
            return fetch(`https://sheetdb.io/api/v1/kv2ann1dhp3kl`)
                .then(r => r.json())
                .then(r => {
                    this.licences = r;
                });
        },

        greaterId() {
            return axios.get(`https://sheetdb.io/api/v1/kv2ann1dhp3kl`)
                .then(r => {
                    this.id = r.data.length + 1;
                    this.editedIndex === -1 ? this.editedItem.ID = this.id : this.editedItem.ID
                });
        },

        editItem (item) {
            this.editedIndex = this.licences.indexOf(item) 
            this.editedItem = Object.assign({}, item)
            this.dialog = true
        },

        deleteItem (item) {
            this.editedIndex = this.licences.indexOf(item)
            this.editedItem = Object.assign({}, item)
            this.dialogDelete = true
        },

        deleteItemConfirm () {
            this.licences.splice(this.editedIndex, 1)
            this.closeDelete()
        },

        close () {
            this.dialog = false
            this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem)
                this.editedIndex = -1
            })
        },

        closeDelete () {
            this.dialogDelete = false
            this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem)
                this.editedIndex = -1
            })
        },

        validateField () {
          this.$refs.form.validate()
        },

        clone () {
            //salva edição
            if (this.editedIndex > -1) {
                this.tempEmpresa = this.editedItem.EMPRESA
                this.tempSolicitante = this.editedItem.SOLICITANTE
                this.tempComercial = this.editedItem.COMERCIAL
                this.tempPlaca = this.editedItem.PLACA
                this.tempEstado = this.editedItem.ESTADO
                this.tempTaxa = this.editedItem.NUMTAXA
                this.tempCusto = this.editedItem.CUSTOTAXA
                this.tempDataPgTaxa = this.editedItem.DATAPAGAMENTOTAXA
                this.tempBanco = this.editedItem.BANCO
                this.tempAET = this.editedItem.AET
                this.tempDataEmissao = this.editedItem.EMISSÃO
                this.tempDataVencimento = this.editedItem.VENCIMENTO
                this.tempCobrado = this.editedItem.VALORCOBRADO
                this.tempSaldo = this.editedItem.SALDO
                this.tempStatus = this.editedItem.STATUS
                this.tempBoleto = this.editedItem.VALORBOLETO
                this.tempVencBoleto = this.editedItem.VENCIMENTOBOLETO
                this.tempObs = this.editedItem.OBSERVAÇÃO
                //3.fechar o popup OK
                setTimeout(() => {
                    this.close()
                }, 300)
                //4.abrir popup de novo pedido
                setTimeout(() => {
                    document.getElementById("newItemBtn").click();
                }, 300)
                //5.popular os campos com as informações salvas anteriormente
                setTimeout(() => {
                    this.editedItem.EMPRESA = this.tempEmpresa
                    this.editedItem.SOLICITANTE = this.tempSolicitante
                    this.editedItem.COMERCIAL = this.tempComercial
                    this.editedItem.PLACA = this.tempPlaca
                    this.editedItem.ESTADO = this.tempEstado
                    this.editedItem.NUMTAXA = this.tempTaxa
                    this.editedItem.CUSTOTAXA = this.tempCusto
                    this.editedItem.DATAPAGAMENTOTAXA = this.tempDataPgTaxa
                    this.editedItem.BANCO = this.tempBanco
                    this.editedItem.AET = this.tempAET
                    this.editedItem.EMISSÃO = this.tempDataEmissao
                    this.editedItem.VENCIMENTO = this.tempDataVencimento
                    this.editedItem.VALORCOBRADO = this.tempCobrado
                    this.editedItem.SALDO = this.tempSaldo
                    this.editedItem.STATUS = this.tempStatus
                    this.editedItem.VALORBOLETO = this.tempBoleto
                    this.editedItem.VENCIMENTOBOLETO = this.tempVencBoleto
                    this.editedItem.OBSERVAÇÃO = this.tempObs
                }, 300)
            } else {
                this.showCloneBtn = false
            }
        },
        save () {
            var form = document.getElementById('sheetdb-form');
            //salva edição
            if (this.editedIndex > -1) {
                axios.put(`https://sheetdb.io/api/v1/kv2ann1dhp3kl/ID/${this.editedIndex + 1}`,{
                    "data": {
                        "EMPRESA": `${this.editedItem.EMPRESA}`,
                        "SOLICITANTE": `${this.editedItem.SOLICITANTE}`,
                        "COMERCIAL": `${this.editedItem.COMERCIAL}`,
                        "PLACA": `${this.editedItem.PLACA}`,
                        "ESTADO": `${this.editedItem.ESTADO}`,
                        "NUMTAXA": `${this.editedItem.NUMTAXA}`,
                        "CUSTOTAXA": `${this.editedItem.CUSTOTAXA}`,
                        "DATAPAGAMENTOTAXA": `${this.editedItem.DATAPAGAMENTOTAXA}`,
                        "BANCO": `${this.editedItem.BANCO}`,
                        "AET": `${this.editedItem.AET}`,
                        "EMISS\u00c3O": `${this.editedItem.EMISSÃO}`,
                        "VENCIMENTO": `${this.editedItem.VENCIMENTO}`,
                        "VALORCOBRADO": `${this.editedItem.VALORCOBRADO}`,
                        "SALDO": `${this.editedItem.SALDO}`,
                        "STATUS": `${this.editedItem.STATUS}`,
                        "NUMBOLETO": `${this.editedItem.NUMBOLETO}`,
                        "VALORBOLETO": `${this.editedItem.VALORBOLETO}`,
                        "VENCIMENTOBOLETO": `${this.editedItem.VENCIMENTOBOLETO}`,
                        "OBSERVA\u00c7\u00c3O": `${this.editedItem.OBSERVAÇÃO}`
                    }
                })
                .then((response) => {
                    if (response.status === 200 || response.status === 201) {
                        this.showUpdate = !this.showUpdate
                        this.fetchLicences();
                        setTimeout(() => {
                            this.showUpdate = !this.showUpdate
                        }, 1500)
                    } else if (response.status === 429) {
                        alert('O limite de solicitações foi excedido! 10.000 licenças foram lançadas esse mês! Entre em contato com o suporte para liberar mais espaço.');
                        window.location.reload();
                    } else if (response.status === 401 || response.status === 403 || response.status === 404) {
                        alert('Erro de servidor. Entre em contato com o suporte.');
                        window.location.reload();
                    } else {
                        this.showError = !this.showError
                        setTimeout(() => {
                            this.showError = !this.showError
                            window.location.reload();
                        }, 1500)
                    }
                })
            }
            //salva novo
            else {
                fetch(form.action, {
                    method : "POST",
                    body: new FormData(form),
                })
                .then((response) => {
                    if (response.status === 200 || response.status === 201) {
                        this.showPost = !this.showPost
                        this.fetchLicences();
                        setTimeout(() => {
                            this.showPost = !this.showPost
                        }, 1500)
                    } else if (response.status === 429) {
                        alert('O limite de solicitações foi excedido! 10.000 licenças foram lançadas esse mês! Entre em contato com o suporte para liberar mais espaço.');
                        window.location.reload();
                    } else if (response.status === 401 || response.status === 403) {
                        alert('Erro de servidor. Entre em contato com o suporte.');
                        window.location.reload();
                    }
                })
                setTimeout(() => {
                    this.editedItem.EMPRESA = ''
                    this.editedItem.SOLICITANTE = ''
                    this.editedItem.COMERCIAL = ''
                    this.editedItem.PLACA = ''
                    this.editedItem.ESTADO = ''
                    this.editedItem.NUMTAXA = ''
                    this.editedItem.CUSTOTAXA = ''
                    this.editedItem.DATAPAGAMENTOTAXA = ''
                    this.editedItem.BANCO = ''
                    this.editedItem.AET = ''
                    this.editedItem.EMISSÃO = ''
                    this.editedItem.VENCIMENTO = ''
                    this.editedItem.VALORCOBRADO = ''
                    this.editedItem.SALDO = ''
                    this.editedItem.STATUS = ''
                    this.editedItem.VALORBOLETO = ''
                    this.editedItem.VENCIMENTOBOLETO = ''
                    this.editedItem.OBSERVAÇÃO = ''
                }, 300)
            }
            this.close()
        }
    },
}
</script>

