import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import AboutView from '../views/AboutView.vue'
import CompanyList from '../views/CompanyList.vue'
import UserList from '../views/UserList.vue'
import LoginScreen from '../views/LoginScreen.vue'
import VehicleList from '../views/VehicleList.vue'
import OrderList from '../views/OrderList.vue'
import NewOrderCustomer from '../views/NewOrderCustomer.vue'
import FinancialControl from '../views/FinancialControl.vue'
import ViaCep from '../components/ViaCep.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/financeiro',
    name: 'FinancialControl',
    component: FinancialControl
  },
  {
    path: '/about',
    name: 'about',
    component: AboutView
  },
  {
    path: '/transportadoras',
    name: 'CompanyList',
    component: CompanyList
  },
  {
    path: '/usuarios',
    name: 'UserList',
    component: UserList
  },
  {
    path: '/login',
    name: 'LoginScreen',
    component: LoginScreen,
    meta: 'login'
  },
  {
    path: '/veiculos',
    name: 'VehicleList',
    component: VehicleList
  },
  {
    path: '/pedidos',
    name: 'OrderList',
    component: OrderList
  },
  {
    path: '/novo-pedido-1',
    name: 'NewOrderCustomer',
    component: NewOrderCustomer
  },
  {
    path: '/novo-pedido-2',
    name: 'NewOrderStaff',
    component: FinancialControl
  },
  {
    path: '/testes',
    name: 'ViaCep',
    component: ViaCep
  },
]

const router = new VueRouter({
  routes
})

export default router